.overlay {
  position: absolute;
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 1;
  visibility: hidden;
}

.overlayVisible {
  visibility: visible;

  .drawer {
    transform: translate(0);
  }
}

.drawer {
  display: flex;
  flex-direction: column;
  position: absolute;
  width: 420px;
  height: 100%;
  right: 0;
  background: #FFFFFF;
  box-shadow: -10px 4px 24px rgba(0, 0, 0, 0.1);
  padding: 30px;
  transform: translate(100%);
  transition: visibility .5s ease-in-out;


  .items {
    flex: 1;
    overflow: auto;
  }

  h2 {
    margin: 0;
  }
}

.cart_item {
  padding: 20px;
  border: 1px solid #F3F3F3;
  border-radius: 20px;
  display: flex;
  align-items: center;

  &_info {
    margin-left: 20px;
    margin-right: 10px;

    h5 {
      font-weight: 400;
      font-size: 15px;
    }

    h5, b {
      margin: 0;
    }
  }

  .remove_btn {
    opacity: .5;
    transition: opacity .2s ease-in-out;
    cursor: pointer;

    &:hover {
      opacity: 1;
    }
  }
}

.items {
  flex: 1;
}

.cart_total_block {
  li {
    display: flex;
    align-items: flex-end;
    margin-bottom: 20px;

    div {
      flex: 1;
      height: 1px;
      border-bottom: 1px dashed #DFDFDF;
      margin: 0 7px;
    }
  }

  .greenButton {
    position: relative;


    img {
      position: absolute;
      right: 35px;
      top: 22px;
      transition: transform .15s ease-in-out;
    }
  }
}

.basketEmpty {
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  flex: 1;
  flex-direction: column;

  button {
    margin-top: 20px;
    width: 245px;
  }
}